@import './config';

.slideContainer{
    overflow: hidden;
    padding: 0 3rem;
    background: $lightash;
}
.slider{
    display: flex;
    width:100%;
    overflow: hidden;
    display: flex;
    padding: .5rem 1rem;
}
.sliderWraper{
    display: flex;
    justify-content: space-around;
    animation: logoLoop 30s linear infinite;


    div{
        margin-left: 1rem;
        padding: 1rem;
        aspect-ratio: 30/9;
        display: flex;
        place-items: center;

        img{
            height: 3rem;
            margin-inline: 2rem;
        }

        .change{
            filter: invert(100%);
        }
    }
}
@keyframes logoLoop{
    from {
        transform: translateX(0);
    }
    to{
        transform: translateX(-100%);
    }
}

header{
    width: 100%;
    
    nav{
        padding: .6rem 3rem;
        height: 4rem;
        align-items: center;
        justify-content: space-between;
        .logo{
            font-size: 23px;
            text-transform: uppercase;
            font-weight: 800;
            line-height: 29.05px;
        }

        ul{
            align-items: center;
            justify-content: space-between;
            li{
                padding-right: 2rem;
            }

            a{
                font-weight: 600;
                font-size: 17px;
                line-height: 20.57px;
                color: $darkAsh;
                text-transform: capitalize;
            }
        }

        button{
            font-weight: 600;
            font-size: 17px;
            line-height: 20.56px;
            width: 184px;
            padding: .7rem 2rem;
            background: $darkBlue;
            color: $primaryColor;
            border-radius: 5px;
            border: none;
        }
    }
    .logo-container{
        // background: red;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }
    .hambuger{
        // background-color: blue;
        cursor: pointer;
        width: 30px;
        height: 40px;
        display: none;
        align-items: center;
        justify-content: flex-end;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .5s ease-in-out;
        -moz-transition: .5s ease-in-out;
        -o-transition: .5s ease-in-out;
        transition: .5s ease-in-out;
    }
      .ham,
      .ham::before,
      .ham::after{
        opacity: 1;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;
      }
    .ham{
        background: black;
        width: 15px;
        height: 3px;
        border-radius: 8px;
        position: relative;
    }
    .hambuger.open .ham {
        background: transparent;
      }
    .ham::before{
        content: "";
        background: black;
        width: 25px;
        height: 3px;
        border-radius: 8px;
        position: absolute;
        top:-8px;
        right: 0;
    }
    .hambuger.open .ham::before {
        opacity: 1;
        top: 0;
        width: 25px;
        -webkit-transform: rotate(135deg);
        -moz-transform: rotate(135deg);
        -o-transform: rotate(135deg);
        transform: rotate(135deg);
      }
    .ham::after{
        background: black;
        content: "";
        width: 19px;
        height: 3px;
        border-radius: 8px;
        position: absolute;
        bottom: -8px;
        right: 0;
    }
      .hambuger.open .ham::after {
        opacity: 1;
        bottom: 0;
        width: 25px;
        -webkit-transform: rotate(-135deg);
        -moz-transform: rotate(-135deg);
        -o-transform: rotate(-135deg);
        transform: rotate(-135deg);
      }

    .hero{
        background:linear-gradient(to bottom, rgba(11,23,87, 0.6), rgba(87,95,139, 0.79)),
        url(../../assets/img/bg-imgg.png) center/cover no-repeat;
        height: calc(660px - 2rem);
        justify-content: center;
        align-items: center;
        color: $primaryColor;

        h1{
            padding: 1rem 0;
            font-size: 35px;
            font-weight: 800;
        }
        span{
            padding-bottom: 1rem;
            font-weight: 500;
            line-height: 24px;
            color: #D9D9D9;
        }
        .bd-btn{
            border-bottom: 3px solid #0B1757;

        }
        p{
            padding: 1rem;
            font-weight: 600;
            font-size: 17px;
            line-height: 21px;
        }

        form{
            max-width: 909px;
            border-radius: 5px;
            margin-top: 2rem;
            background: $primaryColor;
            padding: 0 3rem 1.5rem;
            color: $darkBlue;

            label{
                display: block;
                padding: 1rem 0; 
            }
            input{
                padding: .7rem 1rem;
                height: 2.5rem;
                margin-right: 1rem;
                width: 27rem;
                border: 1px solid $darkAsh;
                border-radius: 5px;
                outline: none;
                font-size: 20px;
            }
            button{
                height: 2.5rem;
                padding: .7rem 1.4rem;
                background: $darkBlue;
                color: $primaryColor;
                border-radius: 5px;
                border: none;
                font-size: 16px;
            }
        }
    }

}

.first{
    width: 100%;
    .conta{
        width: 100%;
        padding: 6rem 3rem;
        justify-content: space-between;
    }
    .fl-conta{
        padding: 1rem;
        width: 40%;
        h1{
            padding: 1rem 0;
            font-weight: 700;
            font-size: 40px;
            line-height: 48px;
            color: #1E1E1E;
            text-transform: capitalize;
        }
        p{
            padding: .3rem 0;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;

        }
        .fl-p{
            margin-top: 1rem;
            padding: .3rem 0;
            text-align: center;
            color: rgb(254,178,28);
            font-weight: bolder;
            font-weight: 700;
            font-size: 36px;
            line-height: 44px;
        }
        span{
            display: block;
            color: $darkAsh;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
        }

        .stats{
            display: flex;
            justify-content: space-between;
        }
    }
    .img-container{
        width: 55%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        
        img{
            width: 90%;
        }
    }

    .grid{
        text-align: center;
        padding: 2rem 3rem;
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 7%;

        article{
            background: white;
            padding: 2rem 1rem;

            h3{
                font-weight: 600;
                font-size: 20px;
                line-height: 24px;
                padding: .4rem 0;
            }
            p{
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
            }
        }
        .icon-container{
            margin: auto;
            height: 3rem;
            width: 4rem;
            background: $lightash;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 1rem;

            .icon{
                font-size: 23px;
            }
        }
    }
}

.seco{
    width: 100%;
    margin-top: 5rem;

    .img-flex{
        padding: 0 3rem;
        background: $darkBlue;
        height: 340px;
        color: white;

        .img-container{
            position: relative;
            width: 60%;

            .img1{
                position: absolute;
                bottom: 0;
                border-top-left-radius: 29px;
                border-top-right-radius: 29px;
            }
            .img2{
                position: absolute;
                top: 0;
                left:214px;
                border-bottom-left-radius: 29px;
                border-bottom-right-radius: 29px;

            }
        }
        .img-text{
            display: flex;
            flex-direction: column;
            justify-content: center;
            // align-items: center;
            width: 50%;

            h3{
                padding: 1rem 0;
                font-weight: 700;
                font-size: 32px;
                line-height: 39px;
            }
            p{
                padding:0.4rem 0 1.4rem 0;
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
                color: rgb(254,178,28);
            }
            button{
                padding: 14px 30px;
                outline: none;
                border: none;
                justify-content: center;
                align-items: center;
                border-radius: 16px;

                i{
                    font-size: 29px;
                    padding-right: 1rem;
                }

                p{
                    padding:0;
                    color: $darkAsh;

                    span{
                        font-weight: bold;
                        font-size: 20px;
                    }
                }
            }
        }
    }

    .tip-conta{
        margin: 4rem auto;
        max-width: 43rem;
        text-align: center;

        h2{
            font-weight: 700;
            font-size: 36px;
            line-height: 44px;
            color: #0B1757;
        }
        p{
            font-weight: 500;
            font-size: 20px;
            padding: .5rem 0;
            text-align: center;

            color: #727272;
        }
    }

    .grid{
        padding: 2rem 3rem;
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 7%;

        .card{
            width: 330px;
            height: 495px;
            background: #FFFFFF;
            border-radius: 14px;
            margin:0 auto ;
            
        }

        img{
            width: 100%;
            height: 290px;
        }
        h3{
            font-weight: 600;
            font-size: 22px;
            line-height: 27px;
            padding: 1rem 0;
        }
        small{
            color: rgb(155, 154, 154);
            padding:1rem 0;
            text-align: left;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
        }
        p{
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            padding: 1rem 0;
        }
    }
    .join{
        margin: 3rem;
        padding: 3rem;
        background: $darkBlue;
        text-align: center;

        h2{
           color: rgb(254,178,28);
        }
        p{
            padding: 1rem 0 2rem;
            color: white;

        }

        form{
            width: 100%;

            .form-group{
                margin: auto;
                max-width: 30rem;
                padding: 3px;
                background: white;
                border-radius: 7px;
            }

            input{
                width: 100%;
                height: 2.8rem;
                padding: .4rem;
                outline:none;
                border: none;
                border-radius: 7px;
                font-size: 20px;
            }
            button{
                height: 2.8rem;
                padding: .7rem 2rem;
                border-radius: 7px;
                outline:none;
                border: none;
                background: $darkBlue;
                color: white;
                font-weight: 600;
                font-size: 16px;
            }
        }
    }
}
footer{
    margin-top: 5rem;
    background: $darkBlue;
    color: white;
    padding: 2rem 3rem;

    .icon{
        font-size: 29px;
    }
    .conta{
        width: 100%;
        padding: 2rem 0 0;
    }
    .foot-cont{
        width: 100%;
        position: relative;
        justify-content: space-between;
    }

    .l-name{
        h2{
            text-transform: uppercase;
        }
        p{
            padding: 1rem 0;
        }

        ul{
            display: flex;
            margin-bottom: 2rem;

            li{
                padding-right: 1rem;
            }
        }
    }

    .grid{
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 7%;

        h3{
            padding: 0;
        }
        p{
            padding: 1rem 0;
        }

        .flex{
            flex-direction: column;
        }
    }

    a{
        color: white;
    }
    .copte{
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 992px) {
    /* … */

    .first .conta{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;

        .fl-conta{
            width: 90%;
            text-align: center;
            margin-bottom: 3rem;
        }
        .img-container{
            width: 80%;
        }
    }
    .first .grid{
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 2rem;
    }

    .seco .img-flex{
        width: 100%;
        height: 30rem;
        position: relative;
        padding: 0 3rem;
        .img-container{
            position: relative;
            width: 60%;

            .img1{
                position: absolute;
                width: 67%;
                // right: 50%;   
                
            }
            .img2{
                width: 67%;
                left: 30%;
            }
        }
        .img-text{
            width: 57%;
            bottom: 3rem;

            .btn-conta{
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }

            button{
                padding: 10px 10%;
                width: 100%;
                margin-bottom: .7rem;

                i{
                    font-size: 22px;
                    padding-right: .6rem;
                }

                p{
                    padding:0;
                    color: $darkAsh;
                    font-size: 13px;

                    span{
                        font-weight: bold;
                        font-size: 15px;
                    }
                }
            }
        }
    }
    .seco .grid{
        grid-template-columns: 1fr 1fr ;
        grid-row-gap: 3rem;
    }
    
    footer .grid{
        grid-template-columns: 1fr;
    }
    footer .foot-cont{
        justify-content: space-between;
    }
}

@media screen and (max-width: 768px) {
    /* … */
    .sliderWraper div img{
        height: 2rem;
    }
    header .hero{
        text-align: center;
        padding: 0 1rem;

        form{
            max-width: 30rem;
            

            .form-group{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }
            input{
                width: 80%;
                margin-bottom: .3rem;
                margin-right: 0;
            }
            button{
                width: 80%;
            }
        }
    }

    header nav{
        flex-direction: column;
        justify-content: space-between;
        ul{
            display: none;
            width: 100%;
            flex-direction: column;
            padding: 1rem 0;
            background: white;
            box-shadow: 0 3px grey;
            justify-content:flex-end;
            transition: all 500ms cubic-bezier(.62,.04,.3,1.56);
            transition-delay: 100ms;
            opacity: 1;
            transform: translatey(-200%);
            z-index: 0;

            li{
                padding: 1rem 0;
            }
        }
        ul.show{
            display: flex;
            opacity: 1;
            transform: translateY(0);
        }
        .hambuger{
            display: flex;
          
        }
    }

    .first .conta .img-container{
        width: 100%;
    }
    .first .grid{
        grid-template-columns: 1fr;
    }
    .seco .img-flex{
        width: 100%;
        position: relative;
        height: 25rem;
        padding: 0 1rem;

        .img-container{
            position: relative;
            width: 60%;
            margin: 0 auto;

            .img1{
                width: 50%;   
                
            }
            .img2{
                width: 50%;
                left: 50%;
            }
        }
        .img-text{
            text-align: center;
            position: absolute;
            top: 15%;
            right: 10%;
            background: linear-gradient(to bottom, rgba(11,23,87, 0.8), rgba(87,95,139, 0.8));
            width: 80%;
            bottom: 3rem;
            padding:0 1rem;

            .btn-conta{
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                button{
                    margin: .4rem .5rem;
                }
            }
        }
    }
    .seco .grid{
        margin: 0 auto;
        max-width: 30rem;
        grid-template-columns: 1fr;

    }

    .seco .join form{
        .form-group{
            background: transparent;
            flex-direction: column;

            input{
                width: 100%;
            }
            button{
                width: 100%;
                border: 1px solid white;
                margin-top: .5rem;
            }
        }
    }
    footer .foot-cont{
        flex-direction: column;
        padding: 0;
    }

    
}

@media screen and (max-width: 600px) {
    header {
        nav{
            padding: .6rem 1rem;
        }
    
        .hero{
            form{
                width: 90%;
                padding-left: 0.5rem;
                padding-right: 0.5rem;
            }
            h1{
                font-size: 25px;
            }
            span{
                font-size:20px;
            }
        }
    }

    .slideContainer{
        padding: 0 1rem;
    }
    .sliderWraper{    
        div{
            img{
                height: 1.8rem;
            }
        }
    }
    .first {
        .conta{
            padding: 6rem 1rem;
        }
        .fl-conta{
            h1{
                font-size: 25px;
                line-height: 40px;
                font-weight: 500;
            }
            p{
                font-size: 15px;
                line-height: 20px;
                font-weight: 300;
            }

            .fl-p{
                font-weight: 50;
                font-size: 26px;
                line-height: 20px;
                font-weight: 400;
            }
            span{
                font-weight: 30;
                font-size: 12px;
                line-height: 14px;
                font-weight: 300;
            }
        }
        .img-container{
            width: 70%;
            
            img{
                width: 90%;
            }
        }
        .grid{
            padding: 2rem 1rem;
        }
    }
    .seco{
        .img-flex{
            padding: 0 1rem;
            width: 100%;

            .img-text{
                width: 100%;
                left: 0;
    
                h3{
                    font-weight: 400;
                    font-size: 25px;
                    line-height: 19px;
                }
                p{
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 16px;
                }
                .btn-conta{
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }
                button{
                    width: 90%;
                    margin-bottom: .6rem;
                    
    
                    i{
                        font-size: 25px;
                        padding-right: 1rem;
                    }
    
                    p{
                        padding:0;
                        color: $darkAsh;
    
                        span{
                            font-weight: bold;
                            font-size: 15px;
                        }
                    }
                }
            }
        }
        .tip-conta{    
            h2{
                font-weight: 500;
                font-size: 27px;
                line-height: 34px;
            }
            p{
                font-weight: 300;
                font-size: 20px;
            }
        }
        .grid{
            margin: 0 auto;
            max-width: 25rem;

            .card{
                width: 100%;
                margin:0 auto ;      
            }
        }

        .join{
            margin: 3rem 1rem;
            padding:2rem 1rem;

        }
    }

    footer{
        padding: 2rem 1rem;
    }

}