@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(249, 249, 249);
  color: rgb(54, 54, 54);
  font-family: "Inter", sans-serif;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.fles-jc-sa {
  justify-content: space-around;
}

.fles-jc-cen {
  justify-content: center;
}

.flex-jc-even {
  justify-content: space-evenly;
}

.flex-jc-sb {
  justify-content: space-between;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  outline: none;
}

.line {
  border-bottom: 3px solid grey;
}

.grid {
  display: grid;
}

form {
  font-size: 18px;
}

.block {
  display: block;
}

.slideContainer {
  overflow: hidden;
  padding: 0 3rem;
  background: rgb(240, 240, 240);
}

.slider {
  display: flex;
  width: 100%;
  overflow: hidden;
  display: flex;
  padding: 0.5rem 1rem;
}

.sliderWraper {
  display: flex;
  justify-content: space-around;
  animation: logoLoop 30s linear infinite;
}
.sliderWraper div {
  margin-left: 1rem;
  padding: 1rem;
  aspect-ratio: 30/9;
  display: flex;
  place-items: center;
}
.sliderWraper div img {
  height: 3rem;
  margin-inline: 2rem;
}
.sliderWraper div .change {
  filter: invert(100%);
}

@keyframes logoLoop {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
header {
  width: 100%;
}
header nav {
  padding: 0.6rem 3rem;
  height: 4rem;
  align-items: center;
  justify-content: space-between;
}
header nav .logo {
  font-size: 23px;
  text-transform: uppercase;
  font-weight: 800;
  line-height: 29.05px;
}
header nav ul {
  align-items: center;
  justify-content: space-between;
}
header nav ul li {
  padding-right: 2rem;
}
header nav ul a {
  font-weight: 600;
  font-size: 17px;
  line-height: 20.57px;
  color: rgb(54, 54, 54);
  text-transform: capitalize;
}
header nav button {
  font-weight: 600;
  font-size: 17px;
  line-height: 20.56px;
  width: 184px;
  padding: 0.7rem 2rem;
  background: rgb(11, 23, 87);
  color: rgb(249, 249, 249);
  border-radius: 5px;
  border: none;
}
header .logo-container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
header .hambuger {
  cursor: pointer;
  width: 30px;
  height: 40px;
  display: none;
  align-items: center;
  justify-content: flex-end;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
header .ham,
header .ham::before,
header .ham::after {
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
header .ham {
  background: black;
  width: 15px;
  height: 3px;
  border-radius: 8px;
  position: relative;
}
header .hambuger.open .ham {
  background: transparent;
}
header .ham::before {
  content: "";
  background: black;
  width: 25px;
  height: 3px;
  border-radius: 8px;
  position: absolute;
  top: -8px;
  right: 0;
}
header .hambuger.open .ham::before {
  opacity: 1;
  top: 0;
  width: 25px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}
header .ham::after {
  background: black;
  content: "";
  width: 19px;
  height: 3px;
  border-radius: 8px;
  position: absolute;
  bottom: -8px;
  right: 0;
}
header .hambuger.open .ham::after {
  opacity: 1;
  bottom: 0;
  width: 25px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
header .hero {
  background: linear-gradient(to bottom, rgba(11, 23, 87, 0.6), rgba(87, 95, 139, 0.79)), url(../../assets/img/bg-imgg.png) center/cover no-repeat;
  height: calc(660px - 2rem);
  justify-content: center;
  align-items: center;
  color: rgb(249, 249, 249);
}
header .hero h1 {
  padding: 1rem 0;
  font-size: 35px;
  font-weight: 800;
}
header .hero span {
  padding-bottom: 1rem;
  font-weight: 500;
  line-height: 24px;
  color: #D9D9D9;
}
header .hero .bd-btn {
  border-bottom: 3px solid #0B1757;
}
header .hero p {
  padding: 1rem;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
}
header .hero form {
  max-width: 909px;
  border-radius: 5px;
  margin-top: 2rem;
  background: rgb(249, 249, 249);
  padding: 0 3rem 1.5rem;
  color: rgb(11, 23, 87);
}
header .hero form label {
  display: block;
  padding: 1rem 0;
}
header .hero form input {
  padding: 0.7rem 1rem;
  height: 2.5rem;
  margin-right: 1rem;
  width: 27rem;
  border: 1px solid rgb(54, 54, 54);
  border-radius: 5px;
  outline: none;
  font-size: 20px;
}
header .hero form button {
  height: 2.5rem;
  padding: 0.7rem 1.4rem;
  background: rgb(11, 23, 87);
  color: rgb(249, 249, 249);
  border-radius: 5px;
  border: none;
  font-size: 16px;
}

.first {
