  justify-content: center;
  align-items: center;
  border-radius: 16px;
}
.seco .img-flex .img-text button i {
  font-size: 29px;
  padding-right: 1rem;
}
.seco .img-flex .img-text button p {
  padding: 0;
  color: rgb(54, 54, 54);
}
.seco .img-flex .img-text button p span {
  font-weight: bold;
  font-size: 20px;
}
.seco .tip-conta {
  margin: 4rem auto;
  max-width: 43rem;
  text-align: center;
}
.seco .tip-conta h2 {
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  color: #0B1757;
}
.seco .tip-conta p {
  font-weight: 500;
  font-size: 20px;
  padding: 0.5rem 0;
  text-align: center;
