}
.seco .grid {
  padding: 2rem 3rem;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 7%;
}
.seco .grid .card {
  width: 330px;
  height: 495px;
  background: #FFFFFF;
  border-radius: 14px;
  margin: 0 auto;
}
.seco .grid img {
  width: 100%;
  height: 290px;
}
.seco .grid h3 {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  padding: 1rem 0;
}
.seco .grid small {
  color: rgb(155, 154, 154);
  padding: 1rem 0;
  text-align: left;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}
.seco .grid p {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  padding: 1rem 0;
}
.seco .join {
  margin: 3rem;
  padding: 3rem;
  background: rgb(11, 23, 87);
  text-align: center;
}
.seco .join h2 {
