.seco .join form {
  width: 100%;
}
.seco .join form .form-group {
  margin: auto;
  max-width: 30rem;
  padding: 3px;
  background: white;
  border-radius: 7px;
}
.seco .join form input {
  width: 100%;
  height: 2.8rem;
  padding: 0.4rem;
