footer {
  margin-top: 5rem;
  background: rgb(11, 23, 87);
  color: white;
  padding: 2rem 3rem;
}
footer .icon {
  font-size: 29px;
}
footer .conta {
  width: 100%;
  padding: 2rem 0 0;
}
footer .foot-cont {
