    width: 100%;
    position: relative;
    height: 25rem;
    padding: 0 1rem;
  }
  .seco .img-flex .img-container {
    position: relative;
    width: 60%;
    margin: 0 auto;
  }
  .seco .img-flex .img-container .img1 {
